<template>
  <div class="paymentWrap">
      <header>
          <router-link to="/msite"><i class="el-icon-arrow-left"></i> </router-link>
          <div class="top_title"><span>在线支付</span></div>
      </header>
      <div class="show_time_amount">
          <section>
              <div class="time_last">支付剩余时间</div>
              <p class="time">00 : {{minute}} : {{second}}</p>
          </section>
      </div>
      <div class="pay_way">选择支付方式</div>
      <section class="pay_way_list">
          <section class="pay_item">
              <div class="pay_icon_contaienr">
                  <div class="zhifubao"></div>
                  <span>支付宝</span>
              </div>
              <i class="choose_icon choosed_way el-icon-success" @click="choose1" :style="active == 1?'color:green':''"></i>
          </section>
          <section class="pay_item">
              <div class="pay_icon_contaienr">
                  <div class="weixin"></div>
                  <span>微信</span>
              </div>
              <i class="choose_icon el-icon-success" @click="choose2" :style="active1 == 2?'color:green':''"></i>
          </section>
      </section>
      <p class="determine" @click="surePay">确认支付</p>
  </div>
</template>

<script>
export default {
name:'Payment',
data(){
    return{
        auth_time:900,
        minute:15,
        second:"00",
        active:0,
        active1:0,
        userid:'',
        cartid:'',
        shopId:'',
        entities:[],
        address_id:'',
        shopMsg:[],
    }
},
created(){
    this.userid = localStorage.getItem('userId');
    console.log(this.userid)
    this.cartid = localStorage.getItem("cartid");
    this.shopMsg = JSON.parse(localStorage.getItem('shopId'));
    this.shopId = this.shopMsg.id;
    this.$store.commit("init_buy_cart");
    this.carArr = [];
    this.shopCarts = this.$store.state.cartList;
        Object.keys(this.shopCarts).forEach(shopId => {
            Object.keys(this.shopCarts[shopId]).forEach(category_id => {
                Object.keys(this.shopCarts[shopId][category_id]).forEach(item_id => {
                    Object.keys(this.shopCarts[shopId][category_id][item_id]).forEach(food_id => {
                            let foodItem = this.shopCarts[shopId][category_id][item_id][food_id];
                            let newFoodItem = {...foodItem, category_id, item_id, food_id, shopId}
                            if (newFoodItem.shopId == this.shopId) {
                                    this.carArr.push(newFoodItem);
                                }
                            });
                        })
                    })
                })
    // console.log(this.carArr)
    for (let i = 0; i < this.carArr.length; i++) {
        let p = [{attrs:[],extra:{},id:this.carArr[i].id,name:this.carArr[i].name,price:this.carArr[i].price,packing_fee:this.carArr[i].packing_fee,quantity:this.carArr[i].num,sku_id:this.carArr[i].sku_id,specs:this.carArr[i].specs,stock:this.carArr[i].stock,}]
        this.entities.push(p)
    }
    let auth_timetimer = setInterval(() => {
                this.auth_time -= 1;
                if (this.auth_time <= 0) {
                    this.isShow2 = true;
                    clearInterval(auth_timetimer);
                }
                this.minute = Math.floor(this.auth_time / 60);
                this.second = this.auth_time % 60;
                if (this.second < 10){
                    this.second = "0"+this.second;
                }
                if (this.minute < 10){
                    this.minute = "0"+this.minute;
                }
            }, 1000);
},
methods:{
    choose1(){
        this.active1 = 0;
        this.active = 1;
    },
    choose2(){
        this.active1 = 2;
        this.active = 0;
    },
    surePay(){
        this.axios.post("v1/users/"+ this.userid +"/carts/"+ this.cartid +"/orders",{
            user_id:this.userid,
            cart_id:this.cartid,
            address_id:19777,
            restaurant_id:this.shopId,
            entities:this.entities
        }).then(res=>{
            console.log(res)
        }).catch(err => {})
        this.$router.push('/confirmorder/payment/order')
    }
}
}
</script>

